* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* color: white; */
}

body {
    color: white;
    background-color: rgb(10, 10, 10);

}

/* Utility */
.dash-line {
    width: 60%;
    /* height: 10%; */
    color: grey;
    margin: 3% 0;
}

.sub-heading {
    margin: 0.8rem 0;
    font-size: 30px;
    font-weight: 600;
}

.edu-exp-logo {
    position: absolute;
    font-size: 20px;
    color: white;
    background-color: rgb(130, 21, 255);
    padding: 2% 2% 2% 2%;
    border-radius: 20px;
    top: 0;
    left: -23px;
}

/* About Main */

.about-main {
    width: 100%;
    height: auto;
    border: 2px solid black;
    background-color: rgb(10, 10, 10);
    display: flex;
    position: relative;
    /* overflow-y: hidden; */
    justify-content: center;
    align-items: center;
    z-index: 2;
}

.about-main-container {
    width: 90%;
    height: 100vh;
    background-color: rgb(10, 10, 10);
    display: flex;
    flex-direction: column;
    /* justify-content: center ; */
    align-items: center;

    /* border: 2px solid white; */
}

.about-header-container {
    width: 100%;
    height: 20%;
    padding: 5% 0;
    /* border: 2px solid rebeccapurple; */
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.transp-sec {
    position: absolute;
    color: rgb(58, 58, 58);
    font-size: 70px;
    opacity: .5;
    font-weight: 800;
    z-index: 1;
}

.head {
    color: white;
    font-size: 40px;
    font-weight: 800;
    z-index: 2;
}

.head span {
    color: rgb(130, 21, 255);
    /* Or your desired hover color */
}

.personal-info-container {
    width: 100%;
    height: 70%;
    /* border: 2px solid red; */
    display: flex;
}

.personal-info-left {
    width: 50%;
    height: 100%;
    /* border: 2px solid green; */
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

}

.info-head {}

.info-container {
    width: 100%;
    height: 75%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    /* border: 2px solid purple; */
}

.info-key-value {
    display: flex;
    width: 46%;
    /* border: 2px solid white; */
    justify-content: start;
    margin: 2% 2%;
}

.info-key {
    margin-right: 3%;
    color: antiquewhite;
    font-weight: 500;
}

.info-value {
    font-size: medium;
}

.download-resume-btn {
    border-radius: 35px;
    cursor: pointer;
    transition: 250ms all ease-in-out;
    text-transform: uppercase;
    text-decoration: none !important;
    position: relative;
    z-index: 1;
    /* padding: 8px 8px; */
    font-size: 17px;
    font-weight: 600;
    color: #fff;
    background-color: #333;
    outline: none !important;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
    padding: 0 0 0 3px;
}

.download-resume-btn:hover {
    background-color: rgb(130, 21, 255);

}

.personal-info-right {
    width: 48%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.count-container {
    text-align: center;
    /* Center the content */
    font-family: sans-serif;
    /* Use a sans-serif font */
    color: rgb(130, 21, 255);
    /* Or your desired hover color */
    border: 1px solid rgb(184, 180, 180);
    border-radius: 20px;
    padding: 5px 25px;
    margin: 0 10px;
}

.count-container h1 {
    font-size: 48px;
    color: rgb(130, 21, 255);

    /* Adjust font size as needed */
    margin-bottom: 10px;
    /* Add spacing between headings */
}

.count-container h2 {
    font-size: 20px;
    /* Adjust font size as needed */
    color: white;
}


/* Myskills section */

.skills-head {}

.skills-container {
    width: 100%;
    /* height: 100%; */
    /* border: 2px solid red; */
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    /* flex-wrap: wrap; */
    padding: 2% 2%;
}

.skill-boxes {
    /* border: 2px solid green; */
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
}

.skill-box {
    margin: 4% 4%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.radialProgressBar {
    border-radius: 50%;
    /* -webkit-transform: translate(50%, 50%); */
    /* transform: translate(50%, 50%); */
    width: 100px;
    height: 100px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background: #575656;
    margin: 20px;
}

.radialProgressBar .overlay {
    border-radius: 50%;
    width: 80px;
    height: 80px;
    margin: auto;
    background: rgb(10, 10, 10);
    text-align: center;
    padding-top: 30%;
}

.progress-10 {
    background-image: -webkit-linear-gradient(36deg, #ddd 50%, transparent 50%), -webkit-linear-gradient(left, rgb(130, 21, 255) 50%, #ddd 50%);
    background-image: linear-gradient(54deg, #ddd 50%, transparent 50%), linear-gradient(90deg, rgb(130, 21, 255) 50%, #ddd 50%);
}

/* Experience and Education  */
.exp-edu-container {
    width: 100%;
    /* height: 89vh; */
    /* border: 2px solid white; */
    display: flex;
    margin-top: 20px;
    justify-content: center;
    align-items: center;
}

.resp-extra {
    display: none;
}

.exp-container {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.exp-box {
    width: 90%;
    border-left: 1px solid rgb(99, 99, 99);
    position: relative;
    padding: 3%;
    padding-top: 0;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
    align-items: start;
    margin: 5px 0;
}

.time-period {
    background-color: rgb(63, 63, 63);
    padding: 4px 6px;
    border-radius: 10px;
    margin-left: 25px;
}

.role {
    font-size: 25px;
    font-weight: 500;
    margin: 10px 0;
}

.company {
    font-size: 20px;
    color: #858585;
}

.job-desc {
    color: #858585;
}


/* Education container */
.edu-container {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.edu-box {
    width: 90%;
    border-left: 1px solid rgb(99, 99, 99);
    position: relative;
    padding: 3%;
    padding-top: 0;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
    align-items: start;
    margin: 5px 0;
}

.course {
    font-size: 25px;
    font-weight: 500;
    margin: 10px 0;
}

.college {
    font-size: 20px;
    color: #858585;
}

.edu-desc {
    color: #858585;
}

/* Responsive */

@media screen and (max-width:576px) {
    .about-main {
        height: 100%;
    }

    .about-header-container {
        padding: 5% 0;
        margin-top: 4rem;
    }

    .about-main-container {
        height: 100%;
    }

    .personal-info-container {
        flex-direction: column;
        height: 32em;
    }

    .transp-sec {
        font-size: 57px;
    }

    .head {
        font-size: 30px;
    }

    .personal-info-left {
        width: 95%;
        height: 100%;
    }

    .info-key-value {
        flex-direction: column;
        margin: 2% 0%;
    }

    .personal-info-right {
        width: 100%;
        height: 100%;
        margin-top: 2rem;
    }

    .count-container {
        padding: 5px 10px;
    }

    .skill-box {
        margin: 0;
    }

    .skills-container {
        margin-top: 20em;
        flex-direction: column;
        height: 100%;
        align-items: center;
    }

    .dash-line {
        margin-top: 2rem;
    }

    .sub-heading {
        font-size: 25px;
    }

    .exp-edu-container {
        flex-direction: column;
    }

    .exp-container {
        width: 100%;
    }

    .edu-container {
        width: 100%;
    }

    .about-header-container {
        padding: 10% 0;
        margin-top: 2rem;
    }

    .transp-sec {
        font-size: 55px;
        opacity: .5;
        font-weight: 800;
        z-index: 1;
    }

    .head {
        font-size: 30px;
    }

    .desk-head {
        display: none;
    }

    .resp-extra {
        display: inline;
    }
}